.title {
  display: flex;
  justify-content: space-around;
  align-items: center;
  background: #fed;
  padding: 0px 10px;
  border-radius: 6px 6px 0 0;
}


.title h3 {
  color: green;
  font-family: 'メイリオ',
    'Meiryo',
    sans-serif;
}

.title h4 {
  color: rgb(120, 175, 37);
}

.title span {
  color: #aaa;
  font-weight: bold;
}

footer .title {
  color: #555;
  font-weight: bold;
}

footer .title span {
  color: #999;
  font-weight: bold;
}

a:hover {
  background: #E3C800;
  color: #fff !important;
  padding: 1px 2px;
}

.title span span {
  color: rgb(0, 170, 255);
  font-weight: bold;
}

.title li {
  /* font-size: x-small; */
  font-weight: 500;
}

.title ul,
.quickview-body ul {
  list-style: disc;
  text-align: left;
}

.quickview-body .message-body {
  font-size: .8rem;
  line-height: 1.4rem;
}

.quickview-footer {
  font-size: medium;
}

@media screen and (max-width: 705px) {
  .quickview-body .message-body {
    font-size: 0.7rem;
  }
}

@media screen and (max-width: 600px) {
  .title {
    /* display: flex; */
    flex-direction: column;
  }

  .title ul {
    font-size: x-small;
    margin-top: 5px;
  }

  .quickview-header {
    height: 2rem !important;
  }

  .quickview-body .message-body {
    font-size: 0.6rem;
  }

  .quickview-footer {
    font-size: small;
  }

  .quickview-footer .arch {
    font-size: small;
  }
}

@media screen and (max-width: 420px) {
  .quickview-body .message-body {
    font-size: 0.5rem;
  }
}

.foot {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  /* align-content: normal; */
}
.card {
  position: relative;
}
.card img {
  width: 100%;
  display: block;
  border: 2px solid #fff;
  border-radius: 6px;
}

/* front of card - the picture */
.card .front {
  transform: rotateY(90deg);
  transition: all ease-in 0.2s;
  position: absolute;
}
.flipped .front {
  transform: rotateY(0deg);
  transition-delay: 0.2s;
}

/* back of card - cover */
.card .back {
  transition: all ease-in 0.2s;
  transition-delay: 0.2s;
}
.flipped .back {
  transform: rotateY(90deg);
  transition-delay: 0s;
}
